// Journal Page
.home-page {
  //   background-color: blue;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  border-top: solid 0.1px rgb(226, 223, 223);
  .journal-page {
    width: 70%;
    display: flex;
    flex-direction: column;
    padding: 30px;
    .picture-content {
      width: 100%;
      height: 550px;
      background-position: bottom;
      background-size: 850px;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;

      h1 {
        width: 50%;
        background-color: #1c7d6b;
        color: white;
        font-size: 36px;
        margin-top: 5%;
      }

      h2 {
        width: 40%;
        background-color: #1c7d6b;
        color: white;
        font-size: 24px;
        margin-left: 58.7%;
        margin-top: 40%;
        text-align: right;
        padding: 5px;
      }
    }

    p {
      line-height: 1.5em;
      height: auto;
      #more {
        display: none;
      }
    }

    button {
      width: 18%;
      display: block;
      margin-left: 30%;
      background-color: white;
      border: 1px solid #0a0a0a;
      padding: 10px;
      color: black;
      margin: 10px;
    }
  }
}
// End of Journal Page
